.myers-datatable {

  position: relative;

  .table-container {
    display: flex;
    flex-direction: column;
  }

  .table-row {
    display: flex;
    width: fit-content;
    position: relative;
  }

  .table-cell {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  .table-header-row {
    z-index: 2;
    position: sticky;
    top: 0px;
    width: fit-content;
  }

  .table-header-panel {
    height: 45px;
  }

  .table-header-panel-row {
    position: absolute;
    height: 45px;
    left: 0px;
    gap: 8px;
    right: calc(0px + 8px);
    /* 0px + scrollbar width */
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
  }

  .table-header-panel-button {
    all: unset;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 4px;
    cursor: pointer;
    font-size: 14px;
  }

  .table-header-panel-button:first-child {
    margin-right: 16px;
  }

  .spinner {
    border: 4px solid #f3f3f3;
    /* Light grey */
    border-top: 4px solid #7e7e7e;
    /* Blue */
    border-radius: 50%;
    width: 15px;
    height: 15px;
    animation: spin 1s linear infinite;
    margin-left: 5px;
    position: sticky;
    left: 0px;
  }

  .spinner-loading-text {
    margin-left: 10px;
    color: rgb(37, 37, 37);
  }

  @keyframes spin {
    0% {
      transform: rotate(0deg);
    }

    100% {
      transform: rotate(360deg);
    }
  }

  .spinner-container {
    position: absolute;
    background-color: white;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    display: flex;
    align-items: center;
    z-index: 1;
  }

  .spinner-wrapper {
    display: flex;
    align-items: center;
    position: sticky;
    left: 0px;
  }

  .popup-floating-container {
    z-index: 2;
    outline: none;
  }
}